.wrapperContactsPage{
    position: relative;
    margin: 24px auto;
    width: 400px;
    padding: 6px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    text-align: center;
    gap: 12px;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2);
}

.titlePhonebook {
    font-size: 1em;
    font-weight: bold;
}

.btnIconPlus {
    position: absolute;
    top: 24px;
    right: 10px;
    border-radius: 50%; 
}